nav {
    display: flex;
    justify-content: center;
    align-items: center;
    background: #1e1e1e;
    height: 5rem;
    margin: 0 auto;
    max-width: 75rem;
    padding: 1rem 2rem;
    position: sticky;
    top: 0;
    width: 100vw;
}

nav ul {
    display: flex;
    color: white;
    cursor: pointer;
    font-weight: bold;
    flex-grow: 1;
    justify-content: center;
}

nav ul a {
    list-style: none;
    margin: 0 1.5rem;
    padding-bottom: 0.5rem;
    font-size: 1rem;
    border-bottom: 3px solid transparent;
}

nav ul a:hover {
    color: rgb(250, 190, 38);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgb(250, 190, 38);
}

nav ul a:active {
    color: rgb(250, 190, 38);
    padding-bottom: 0.5rem;
    border-bottom: 3px solid rgb(250, 190, 38);
}

button {
    display: flex;
    justify-content: center;
    align-items: center;
    background: white;
    margin-left: auto;
    padding: 10px;
    border: none;
    border-radius: 2rem;
    cursor: pointer;
    font-weight: bold;
    font-size: 1rem;
    width: 160px;
}

button:hover {
    background: rgb(250, 190, 38);
}

button span {
    margin-right: 10px;
    font-size: 1.2rem;
}